.footer {
  position: relative;
  padding: 20px 0;
  background: #acb6e5; 
  background: -webkit-linear-gradient(to right, #84e2d1, #8193c1); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #84e2d1, #8193c1); /* 한 톤 더 진한 색상 */
}

.footer::after {
  content: ''; /* 가상 요소 생성 */
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* 50% 투명도의 검은색 배경 */
  z-index: -1; /* 가상 요소를 뒤로 보냄 */
}

.footer .container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer-content {
  display: flex;
  flex-direction: column; /* 수직 방향으로 정렬 */
  justify-content: center; /* 수직 가운데 정렬 */
  align-items: center; /* 수평 가운데 정렬 */
  width: 100%;
  max-width: 1200px;
}

.footer-logo img {
  width: 80px;
}

.footer-links ul {
  list-style: none;
  padding: 0;
  display: flex;
  justify-content: center; /* 링크를 수평 가운데 정렬 */
}

.footer-links li {
  margin: 0 15px;
}

.footer-links a {
  color: #fff;
  text-decoration: none;
  transition: color 0.3s;
}

.footer-links a:hover {
  color: #ddd;
}

.footer-social a {
  color: #fff;
  margin: 0 10px;
  font-size: 20px;
  transition: color 0.3s;
}

.footer-social a:hover {
  color: #ddd;
}

.footer-bottom {
  margin-top: 20px;
  text-align: center;
}
