/* artist.css 파일 */

.item {
    cursor: pointer;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    transition: width 0.5s ease;
    position: relative;
    border-radius: 50%;
    overflow: hidden;
}

.artist-info {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.8);
    color: #fff;
    padding: 10px;
    transition: bottom 0.3s ease;
    border-radius: 10px;
}

.artist-row {
    display: flex;
    justify-content: center;
}

.expanded {
    z-index: 1;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    max-width: 15%;
    max-height: 100%;
}

.expanded-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 2;
}

.social-icon {
    width: 30px;
    height: auto;
    margin-top: 5px;
}

/* 반응형 스타일링 */
@media (max-width: 768px) {
    .expanded {
        max-width: 50%; /* 모바일 화면에서 너비를 조정합니다. */
    }

    .artist-info {
        padding: 5px; /* 모바일 화면에서 정보 패널의 패딩을 줄입니다. */
    }

    .artist-info h5,
    .artist-info p {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: normal;
        word-wrap: break-word;
        max-height: 3.6em; /* 텍스트가 길 경우 두 줄까지만 보여줍니다. */
        line-height: 1.8em; /* 라인 간격을 설정합니다. */
    }
}

/* 텍스트 줄 바꿈 처리 */
.artist-info h5,
.artist-info p {
    margin: 0;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-wrap: break-word;
}
