.loginform-container {
    max-width: 400px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    background-color: #f9f9f9;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .login-title {
    font-size: 2rem;
    text-align: center;
    margin-bottom: 20px;
  }
  
  .login-form {
    display: flex;
    flex-direction: column;
  }
  
  .form-label {
    margin-bottom: 8px;
  }
  
  .form-input {
    padding: 10px;
    margin-bottom: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 1rem;
  }
  
  .form-button {
    padding: 12px;
    border: none;
    border-radius: 4px;
    background-color: #A7ECEE;
    color: black;
    font-size: 1rem;
    cursor: pointer;
  }
  
  .form-button:hover {
    color: white;
  }
  
  .error-message {
    color: red;
    font-size: 0.875rem;
    margin-top: 8px;
  }
  