@font-face {
    font-family: 'PartialSansKR-Regular';
    src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_2307-1@1.1/PartialSansKR-Regular.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
  }
  
  .header {
    font-family: 'PartialSansKR-Regular';
    display: flex;
    top: 0;
    width: 100%;
    justify-content: space-between; /* 좌우 간격을 최대한 확보 */
    align-items: center; /* 수직 가운데 정렬 */
    background: #acb6e5; /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #b8f5ea, #acb6e5); /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #b8f5ea, #acb6e5);
    padding: 10px 20px; /* 상하좌우 여백 추가 */
  }
  
  .myshinee {
    font-size: 30px;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
    -webkit-text-stroke: 1px black;
    color: white;
    text-decoration: none;
    font-weight: bold;
  }
  
  .myshinee:hover {
    color: white;
  }
  
  .nav-links {
    display: flex;
    list-style-type: none;
    margin: 0;
    padding: 0;
    width: 100%; /* 부모 요소인 .header에 맞추기 위해 너비 100% 설정 */
    justify-content: flex-end; /* 링크를 오른쪽 정렬 */
  }
  
  .nav-links li {
    margin-right: 20px;
  }
  
  .nav-links li:last-child {
    margin-right: 0;
  }
  
  .nav-links .nav-link {
    margin-right: 20px; /* 각 링크 간의 우측 여백 추가 */
    text-decoration: none; /* 링크 밑줄 제거 */
    color: white; /* 링크 텍스트 색상 설정 */
  }
  
  .nav-links .nav-link.active {
    font-weight: bold; /* 활성 탭의 글꼴을 굵게 표시합니다. */
  }
  
  /* 반응형 스타일 */
  @media (max-width: 768px) {
    .header {
      flex-direction: column; /* 작은 화면에서는 세로로 정렬합니다. */
      align-items: stretch; /* 세로로 나열된 아이템들이 너비를 최대한 활용하도록 설정합니다. */
      padding: 10px 0; /* 상하 여백을 줄입니다. */
    }
  
    .nav-links {
      margin-top: 10px; /* 네비게이션 링크 사이의 간격을 늘립니다. */
      flex-direction: column; /* 작은 화면에서는 세로로 정렬합니다. */
      justify-content: flex-start; /* 링크를 위에서부터 정렬 */
      align-items: flex-start; /* 링크를 왼쪽 정렬 */
    }
  
    .nav-links .nav-link {
      margin-right: 0; /* 각 링크 간의 우측 여백을 제거합니다. */
      margin-bottom: 10px; /* 각 링크 사이의 여백을 추가합니다. */
    }
  
    .login-container {
      margin-top: 10px; /* 로그인 버튼과의 간격을 조정합니다. */
    }
  
    .header .logo {
      text-align: center; /* 로고를 가운데 정렬합니다. */
      margin-bottom: 10px; /* 로고와 네비게이션 사이의 간격을 조정합니다. */
    }
  
    .myshinee {
      font-size: 24px; /* 작은 화면에서 폰트 크기를 줄입니다. */
    }
  }
  