.toptrack {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.track-list {
  list-style: none;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
}

.track-item {
  flex: 0 0 calc(33.33% - 20px); /* 가로로 3개씩 배치하고 간격을 조정합니다 */
  margin-right: 20px;
  margin-bottom: 20px;
  padding: 10px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column; /* 아이템 내의 요소들을 세로로 배열합니다 */
  align-items: center; /* 아이템 내의 요소들을 수평으로 가운데 정렬합니다 */
  text-align: center; /* 텍스트를 가운데 정렬합니다 */
}

.toptrack img {
  border-radius: 0; /* 동그라미를 없애기 위해 border-radius를 0으로 설정 */
  margin-bottom: 10px; /* 이미지 아래 여백 추가 */
  display: block; /* 이미지를 블록 요소로 설정 */
  width: 100%;
  height: auto;
  max-width: 100%; /* 이미지가 컨테이너를 넘지 않도록 최대 너비 설정 */
  max-height: 350px; /* 원하는 높이로 설정 */
  object-fit: cover; /* 이미지의 비율 유지를 위해 cover 값을 사용 */
}


.track-info {
  flex: 1;
  display: flex;
  flex-direction: column; /* 텍스트를 수직으로 정렬 */
  justify-content: center; /* 텍스트를 수직 중앙 정렬 */
  align-items: center; /* 수평 가운데 정렬 */
  margin-top: 10px; /* 버튼 위 여백 설정 */
}

.track-name {
  font-weight: bold; /* 트랙 제목을 굵게 표시 */
  margin-bottom: 5px; /* 텍스트 아래 여백 추가 */
}

.track-item button:hover {
  background-color: #0056b3; /* 마우스 호버 시 배경색 변경 */
}


.track-item button {
  width: 80px; /* 버튼 너비 설정 */
  height: 40px; /* 버튼 높이 설정 */
  margin-top: 10px; /* 버튼 위 여백 설정 */
  cursor: pointer; /* 포인터 커서 설정 */
  border: 2px solid #000000; 
}

.track-item button:hover {
  background-color: #000000; /* 마우스 호버 시 배경색 변경 */
  color: white;
}
