.fanletter-container {
  max-width: 600px;
  height: 600px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 8px;
  background-color: #f9f9f9;
}

.fanletter-header {
  text-align: center;
  margin-bottom: 20px;
}

.fanletter-form {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.fanletter-form input[type="text"] {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.fanletter-form button {
  padding: 10px;
  border: none;
  border-radius: 4px;
  background-color: #A7ECEE;
  color: black;
  cursor: pointer;
}

.fanletter-form button:hover {
  color: white;
}

.fanletter-list {
  list-style-type: none;
  padding: 0;
}

.fanletter-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  border-bottom: 1px solid #ccc;
}

.fanletter-item:last-child {
  border-bottom: none;
}

.fanletter-item input[type="text"] {
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-right: 10px;
}

.fanletter-item button {
  padding: 5px 10px;
  border: none;
  border-radius: 4px;
  background-color: #A7ECEE;
  color: black;
  cursor: pointer;
}

.fanletter-item button:hover {
  color: white;
}

.fanletter-item .delete-button {
  background-color: #FFB1B1;
}

.fanletter-item div {
  display: flex;
  gap: 10px;
}

/* 푸터와의 간격 설정 */
.fanletter-container + .footer {
  margin-top: 20px;
}